import Link from "next/link";

const styles = `
  :root {
    --bg: #f7f8fb;
    --card: #ffffff;
    --border: #e7eaf0;
    --muted: #6b7280;
    --text: #111827;
    --primary: #2563eb;
  }

  body { background: var(--bg); }

  .hero {
    display: grid;
    grid-template-columns: 1.2fr 0.8fr;
    gap: 32px;
    align-items: center;
  }

  .container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 32px 20px 48px;
  }

  .badge {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    font-size: 12px;
    color: var(--primary);
    background: #eaf1ff;
    border: 1px solid #d7e4ff;
    padding: 6px 10px;
    border-radius: 999px;
  }

  .title {
    font-size: 40px;
    line-height: 1.1;
    margin: 14px 0 12px;
    letter-spacing: -0.02em;
  }

  .subtitle { color: var(--muted); font-size: 16px; }

  .cta {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    background: var(--primary);
    color: #fff;
    padding: 12px 16px;
    border-radius: 10px;
    border: 1px solid transparent;
    text-decoration: none;
    font-weight: 600;
    box-shadow: 0 1px 2px rgba(0,0,0,0.06);
  }

  .cta-secondary {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    background: #fff;
    color: #111827;
    padding: 12px 16px;
    border-radius: 10px;
    border: 1px solid var(--border);
    text-decoration: none;
    font-weight: 600;
  }

  .panel {
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 1px 2px rgba(0,0,0,0.04), 0 8px 24px rgba(17,24,39,0.04);
  }

  .marquee {
    position: relative;
    overflow: hidden;
    background: #fff;
    border: 1px solid var(--border);
    border-radius: 12px;
  }
  .marquee-inner {
    display: flex;
    gap: 40px;
    padding: 14px 16px;
    min-width: 200%;
    animation: scroll 20s linear infinite;
  }
  .chip {
    border: 1px solid #e5e7eb;
    background: #f9fafb;
    border-radius: 999px;
    padding: 8px 12px;
    white-space: nowrap;
    font-size: 14px;
  }
  @keyframes scroll { 0% { transform: translateX(0); } 100% { transform: translateX(-50%); } }

  @media (max-width: 900px) {
    .hero { grid-template-columns: 1fr; }
    .title { font-size: 32px; }
  }
`;

